<!--  -->
<template>
  <div class="Tags">
    <el-tag
      :key="item.label"
      v-for="item in tags"
      :closable="item.label !== '首页'"
      :disable-transitions="false"
      @close="closeTag(item)"
      @click="changeMenu(item)"
      :effect="$route.path === item.path ? 'dark' : 'plain'"
    >
      {{ item.label }}
    </el-tag>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import { mapState, mapMutations } from 'vuex'
export default {
  //import引入的组件需要注入到对象中才能使用
  name: 'Tags',
  components: {},
  props: {},
  data() {
    //这里存放数据
    return {
      inputVisible: false,
      inputValue: ''
    }
  },
  //监听属性 类似于data概念
  computed: {
    ...mapState({
      tags: state => state.tab.tagsList
    })
  },
  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
    ...mapMutations(['selectMenu', 'closeTag']),
    changeMenu(val) {
      this.$router.push(val.path)
      this.selectMenu(val)
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {},
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {},
  beforeCreate() {}, //生命周期 - 创建之前
  beforeMount() {}, //生命周期 - 挂载之前
  beforeUpdate() {}, //生命周期 - 更新之前
  updated() {}, //生命周期 - 更新之后
  beforeDestroy() {}, //生命周期 - 销毁之前
  destroyed() {}, //生命周期 - 销毁完成
  activated() {} //如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style lang="scss" scoped>
.Tags {
  padding: 10px;
  .el-tag {
    margin-right: 15px;
    margin-bottom: 10px;
    cursor: pointer;
  }
}
/* @import url(); 引入公共css类'*/
</style>
